<template>
  <BaseLayout>
    <template #navbar>
      <Navbar :breadcrumb="breadcrumb" />
    </template>
    <template #sidebar>
      <Sidebar class="sidebar" :sections="sections" />
    </template>
    <template #content>
      <ContentLayout>
        <BillingAlert
          v-if="billingMetadata"
          :billing-metadata="billingMetadata"
          :organization-id="organizationId"
        />
        <RouterView />
      </ContentLayout>
    </template>
  </BaseLayout>
</template>

<script setup lang="ts">
import Navbar from '@/apps/console/components/workspace/Navbar.vue';
import BaseLayout from '@/components/BaseLayout.vue';
import ContentLayout from '@/components/ContentLayout.vue';
import { asyncComputed } from '@/utils/asyncComputed';
import { PhCreditCard, PhCube, PhUsersThree } from '@phosphor-icons/vue';
import { computed } from 'vue';
import { useRoute } from 'vue-router';
import { OrganizationModel } from '../apis/cloud';
import BillingAlert from '../components/workspace/BillingAlert.vue';
import Sidebar, { type Section } from '../components/workspace/Sidebar.vue';

const route = useRoute();

const organizationId = route.params.organizationId as string;

const { result: organization } = asyncComputed(() => OrganizationModel.get(organizationId));

const breadcrumb = computed(() =>
  organization.value
    ? [
        {
          label: 'My organizations',
          path: '/organizations',
        },
        {
          label: organization.value.name,
          path: `/organizations/${organization.value.id}`,
        },
      ]
    : undefined,
);

const billingMetadata = computed(() => organization.value?.billingMetadata);

const sections = [
  {
    name: 'Organization',
    items: [
      {
        name: 'Projects',
        icon: PhCube,
        path: 'projects',
      },
      {
        name: 'Editors',
        icon: PhUsersThree,
        path: 'editors',
      },
      {
        name: 'Billing',
        icon: PhCreditCard,
        path: 'billing',
      },
    ],
  },
] satisfies Section[];
</script>
